.calculator {
  max-width: 50%;
  margin: 50px auto;
  border: 1px solid #ccc;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 0 10px white;
  
}

.display {
  background-color: #f8f8f8;
  padding: 0px;
  text-align: center;
}

.display h1 {
  margin: 0;
  font-size: 18px;
  color: white;
  font-Family:"fantasy";
}

.display input {
  width: calc(100% - 20px);
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 16px;
}

.result {
  font-size: 24px;
  font-weight: bold;
  color: #333;
}

.buttons {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

button {
  width: 70%;
  padding: 5px;
  margin: 5px;
  border: none;
  border-radius: 4px;
  font-size: 18px;
  cursor: pointer;
  background-color: green;
}

button:active {
  background-color: green;
}
button:hover{
  background-color: greenyellow;
}
.clear {
  grid-column: span 4;
  background-color:rgb(47, 183, 47) ;
  color: #fff;
  width: 90%;
}

/* Collapsed Style */
@media (max-width: 600px) {
  .calculator {
    width: 100%;
    border-radius: 0;
  }

  .display input {
    width: 100%;
  }

  .buttons {
    grid-template-columns: repeat(2, 1fr);
  }

  button {
    font-size: 16px;
  }
}
